//
// List groups
// --------------------------------------------------


// Base class
//
// Easily usable on <ul>, <ol>, or <div>.

[tb-list-group] {
  // No need to set list-style: none; since [tb-list-group-item] is block level
  margin-bottom: 20px;
  padding-left: 0; // reset padding because ul and ol
}


// Individual list items
//
// Use on `li`s or `div`s within the `[tb-list-group]` parent.

[tb-list-group-item] {
  position: relative;
  display: block;
  padding: 10px 15px;
  // Place the border on the list items and negative margin up for better styling
  margin-bottom: -1px;
  background-color: @list-group-bg;
  border: 1px solid @list-group-border;

  // Round the first and last items
  &:first-child {
    .border-top-radius(@list-group-border-radius);
  }
  &:last-child {
    margin-bottom: 0;
    .border-bottom-radius(@list-group-border-radius);
  }

  // Align badges within list items
  > [tb-badge] {
    float: right;
  }
  > [tb-badge] + [tb-badge] {
    margin-right: 5px;
  }
}


// Linked list items
//
// Use anchor elements instead of `li`s or `div`s to create linked list items.
// Includes an extra `[tb-list-group~='active']` modifier class for showing selected items.

a[tb-list-group-item] {
  color: @list-group-link-color;

  [tb-list-group-item][tb-list-group-heading] {
    color: @list-group-link-heading-color;
  }

  // Hover state
  &:hover,
  &:focus {
    text-decoration: none;
    color: @list-group-link-hover-color;
    background-color: @list-group-hover-bg;
  }
}

[tb-list-group-item] {
  // Disabled state
  &[tb-list-group~='disabled'],
  &[tb-list-group~='disabled']:hover,
  &[tb-list-group~='disabled']:focus {
    background-color: @list-group-disabled-bg;
    color: @list-group-disabled-color;
    cursor: not-allowed;
    
    // Force color to inherit for custom content
    [tb-list-group-item][tb-list-group-heading] {
      color: inherit;
    }
    [tb-list-group-item][tb-list-group-text] {
      color: @list-group-disabled-text-color;
    }
  }

  // Active class on item itself, not parent
  &[tb-list-group~='active'],
  &[tb-list-group~='active']:hover,
  &[tb-list-group~='active']:focus {
    z-index: 2; // Place active items above their siblings for proper border styling
    color: @list-group-active-color;
    background-color: @list-group-active-bg;
    border-color: @list-group-active-border;

    // Force color to inherit for custom content
    [tb-list-group-item][tb-list-group-heading],
    [tb-list-group-item][tb-list-group-heading] > small,
    [tb-list-group-item][tb-list-group-heading] > [tb-list-group~='small'] {
      color: inherit;
    }
    [tb-list-group-item][tb-list-group-text] {
      color: @list-group-active-text-color;
    }
  }
}


// Contextual variants
//
// Add modifier classes to change text and background color on individual items.
// Organizationally, this must come after the `:hover` states.

.list-group-item-variant(success; @state-success-bg; @state-success-text);
.list-group-item-variant(info; @state-info-bg; @state-info-text);
.list-group-item-variant(warning; @state-warning-bg; @state-warning-text);
.list-group-item-variant(danger; @state-danger-bg; @state-danger-text);


// Custom content options
//
// Extra classes for creating well-formatted content within `[tb-list-group-item]`s.

[tb-list-group-item][tb-list-group-heading] {
  margin-top: 0;
  margin-bottom: 5px;
}
[tb-list-group-item][tb-list-group-text] {
  margin-bottom: 0;
  line-height: 1.3;
}
