// Embeds responsive
//
// Credit: Nicolas Gallagher and SUIT CSS.

[tb-embed-responsive] {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;

  [tb-embed-responsive-item],
  iframe,
  embed,
  object {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    border: 0;
  }

  // Modifier class for 16:9 aspect ratio
  &[tb-embed-responsive-16by9] {
    padding-bottom: 56.25%;
  }

  // Modifier class for 4:3 aspect ratio
  &[tb-embed-responsive-4by3] {
    padding-bottom: 75%;
  }
}
