//
// Pager pagination
// --------------------------------------------------


[tb-pager] {
  padding-left: 0;
  margin: @line-height-computed 0;
  list-style: none;
  text-align: center;
  &:extend([tb-clearfix] all);
  li {
    display: inline;
    > a,
    > span {
      display: inline-block;
      padding: 5px 14px;
      background-color: @pager-bg;
      border: 1px solid @pager-border;
      border-radius: @pager-border-radius;
    }

    > a:hover,
    > a:focus {
      text-decoration: none;
      background-color: @pager-hover-bg;
    }
  }

  [tb-pager~='next'] {
    > a,
    > span {
      float: right;
    }
  }

  [tb-pager~='previous'] {
    > a,
    > span {
      float: left;
    }
  }

  [tb-pager~='disabled'] {
    > a,
    > a:hover,
    > a:focus,
    > span {
      color: @pager-disabled-color;
      background-color: @pager-bg;
      cursor: not-allowed;
    }
  }

}
